import { Component, OnInit } from '@angular/core';
import { WorkerformService } from '../services/worker-form.service'

@Component({
  selector: 'app-worker-form',
  templateUrl: './worker-form.component.html',
  styleUrls: ['./worker-form.component.css']
})
export class WorkerFormComponent {

  showErrMsg1 = false;
  showErrMsg2 = false;
  submitted: boolean;
  registerForm: any;
  error = '';


  mname: any;
  amob: any;
  gender: any;
  anumber: any;
  workexperience: any;
  relocate: any;
  hedu: any;
  lname: any;
  mob: any;
  address: any;
  paddress: any;
  dob: any;
  SkillsSet: any;
  Education: any;
  Familycount: any;
  fname: any;
  employed: any;
  dailywaged: any;
  choice=['Yes','No']
  genderVals=['Male','Female','Others']



  constructor(private workerFormService :WorkerformService) { }


  isNameSelected: boolean;
  // fname: any;
  required: boolean;




  selectInput(event) {
    const selected = event.target.value;
    // tslint:disable-next-line: triple-equals
    if (selected == 'No') {
      this.isNameSelected = true;
    } else {
      this.isNameSelected = false;
    }
  }

  keyPress(event: any) {
    const pattern = /[0-9\+\-\ ]/;

    const inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }


  save() {
    if (this.fname == 'undefined' || this.fname == null) {
      this.error = '* Please enter first name';
    } else if (this.lname == 'undefined' || this.lname == null) {
      // this.required = true;
      this.error = '* Please enter last name';
    } else if (this.mob == null) {
      this.required = true;
      this.error = '* Please enter Mobile Number';
    } else if (this.address == 'undefined' || this.address == null) {
      this.required = true;
      this.error = '* Please Enter Current Address with pincode';
    } else if (this.paddress == null) {
      this.required = true;
      this.error = '* Please Enter Permanent Address with pincode';
    } else if (this.dob == null) {
      this.required = true;
      this.error = '* Please Enter date of birth';
    } else if (this.SkillsSet == 'undefined' || this.SkillsSet == null) {
      this.required = true;
      this.error = '* Please add your SkillsSet';
    } else if (this.workexperience == null) {
      this.required = true;
      this.error = '* Please Enter your work experience';
    } else if (this.Education == null) {
      this.required = true;
      this.error = '* Please Enter Education Details';
    } else if (this.Familycount == null) {
      this.required = true;
      this.error = '* Please Enter Familycount';
    }


    const Data: any = {
      firstName: this.fname,
      middleName: this.mname,
      lastName: this.lname,
      mobNum: this.mob,
      aMobNum: this.amob,
      gender: this.gender,
      adhaar: this.anumber,
      cAddress: this.address,
      pAddress: this.paddress,
      dob: this.dob,
      skillset: this.SkillsSet,
      employed: this.employed,
      highestEducation: this.Education,
      Workexperience: this.workexperience,
      opentoRelocate: this.relocate,
      dailyWagedEmployee: this.dailywaged,
      highestDegree: this.hedu,
      numberOfFamilyMembers: this.Familycount
    };
    console.log(Data);
    this.workerFormService.insertData(Data).subscribe(
      resp => {
       console.log(resp)
      }
    );
  }


  onReset() {
    this.submitted = false;
    this.fname = [];
    this.lname = [];
    this.mname = [];
    this.mob = [];
    this.amob = [];
    this.gender = [];
    this.address = [];
    this.paddress = [];
    this.dob = [];
    this.anumber = [];
    this.SkillsSet = [];
    this.workexperience = [];
    this.relocate = [];
    this.Education = [];
    this.hedu = [];
    this.Familycount = [];
  }
}
